
import "xterm/css/xterm.css";

import {
  computed,
  defineComponent,
  nextTick,
  onMounted,
  reactive,
  ref,
  toRefs,
  watch,
  watchEffect,
} from "vue";
import xhttp, { Xhttp } from "../xhttp";
import { Terminal } from "xterm";
import { AttachAddon } from "xterm-addon-attach";
import { FitAddon } from "xterm-addon-fit";
import { ElMessage, ElMessageBox, ElPopover } from "element-plus";
import { Router, useRoute, useRouter } from "vue-router";

let route: Router;

enum Mode {
  "create" = 0,
  "update" = 1,
}

interface HostInfo {
  ip: string;
  username: string;
  port: number;
  session_id: string;
  shell: string;
  start_time: string;
}

let data = reactive({
  active_name: "connectInfo",
  host_list: [] as Array<HostInfo>,
  is_login: false,
  password: "",

  old_password: "",
  new_password_a: "",
  new_password_b: "",

});

/**
 * 断开链接
 */
function disconnect(index: number, row: HostInfo) {
  let xhttp = new Xhttp();
  xhttp
    .delete(`/api/status?session_id=${row.session_id}`)
    .then((response) => response.json())
    .then((json) => {
      if (json.code === 0) {
        getStatus();
      }
    });
}

function getStatus() {
  let xhttp = new Xhttp();
  xhttp
    .get("/api/status")
    .then((response) => {
      if (response.status === 401) {
        // alert("没有登陆");
        data.is_login = false;
      }
      return response.json();
    })
    .then((json) => {
      if (json.code === 0) {
        data.is_login = true;
        data.host_list = json.data;
      }
    });
}

function login() {
  let fm = new FormData();
  fm.append("pwd", data.password);
  let xhttp = new Xhttp();
  xhttp
    .post("/api/login", { body: fm })
    .then((response) => {
      if (response.status === 401) {
        ElMessage.error("密码错误");
        return;
      }
      return response.json();
    })
    .then((json) => {
      if (json.code === 0) {
        data.is_login = true;
        getStatus();
      }
    });
}

function changePassword() {
  if (data.old_password.length === 0) {
    ElMessage.error("旧密码不能为空");
    return;
  }

  if (data.new_password_a.length === 0) {
    ElMessage.error("新密码不能为空");
    return;
  }

  if (data.new_password_b.length === 0) {
    ElMessage.error("确认新密码不能为空");
    return;
  }

  if (data.new_password_a !== data.new_password_b) {
    ElMessage.error("两次密码输入不一致");
    return;
  }

  let fm = new FormData();
  fm.append("old_pwd", data.old_password);
  fm.append("new_pwd", data.new_password_a);
  let xhttp = new Xhttp();
  xhttp
    .patch("/api/login", { body: fm })
    .then((response) => response.json())
    .then((json) => {
      if (json.code === 0) {
        ElMessage.success("密码修改成功");
        data.is_login = false;
      }
    });
}

// 报告连接状态
function refreshConnectInfo() {
  if (data.is_login) {
    getStatus();
    setInterval(getStatus, 10000);
  }
}

export default defineComponent({
  name: "manage",

  setup(props: any, context) {
    return {
      ...toRefs(data),
      login,
      disconnect,
      changePassword,
    };
  },
});
